import React, {Component} from 'react';
import Node from './Node';

export default class Tree extends Component {
  render() {
    return (
      <ul>
        {this.props.list.map((node) => {
          if (node.mates) {
            return (
              <Node
                key={node.name}
                name={node.name}
              >
                {node.mates}
              </Node>
            );
          } else {
            return (
              <Node
                key={node.name}
                name={node.name}
              >
                {node.kids}
              </Node>
            );
          }
        })}
      </ul>
    );
  }
}


