import React, {Component} from 'react';
import Tree from './Tree';
import list from '../../.list.json';

export default class TreeDiagram extends Component {
  render() {
    return (
      <div>
        <div className="collapse navbar-toggleable-xs tree-diagram">
          <Tree list={list}/>
        </div>
        <div className="hidden-sm-up alert alert-info" role="alert">
          建議瀏覽解析度為<strong>1024 x 768</strong>，或以上。
        </div>
      </div>

    );
  }
}


