import React, {Component} from 'react';
import Tree from './Tree';

export default class Node extends Component {
  render() {
    return (
      <li>
        <a href="#">
          {this.props.name}
        </a>
        <Tree list={this.props.children}/>
      </li>
    );
  }
}


