import React, {Component} from 'react';

export default class NavBar extends Component {
  render() {
    return (
      <nav className="navbar navbar-light head">
        <button className="navbar-toggler hidden-md-up" type="button" data-toggle="collapse"
                data-target="#menu"></button>
        <span className="navbar-brand float-md-left float-xs-right">族譜</span>
        <div className="collapse navbar-toggleable-sm" id="menu">
          <ul className="nav navbar-nav float-md-right">
            <li className="nav-item"><a href="#" className="nav-link">登入</a></li>
            <li className="nav-item"><a href="#" className="nav-link">聯絡管理員</a></li>
          </ul>
        </div>
      </nav>
    );
  }
}


